import React from 'react';
import './AnimateTextContainer.scss';
import anime from 'animejs';

export default class AnimateTextContainer extends React.Component {
  strText = "FULL STACK ENGINEER";
  textIntervals = [];
  constructor(props) {
    super(props);
  }
  /* Custom Methods */
  loadText() {
    anime.timeline({ loop: true })
      .add({
        targets: '.ml8 .circle-white',
        scale: [0, 3],
        opacity: [1, 0],
        easing: "easeInOutExpo",
        rotateZ: 360,
        duration: 1100
      }).add({
        targets: '.ml8 .circle-container',
        scale: [0, 1],
        duration: 1100,
        easing: "easeInOutExpo",
        offset: '-=1000'
      }).add({
        targets: '.ml8 .circle-dark',
        scale: [0, 1],
        duration: 1100,
        easing: "easeOutExpo",
        offset: '-=600'
      }).add({
        targets: '.ml8 .letters-left',
        scale: [0, 1],
        duration: 1200,
        offset: '-=550'
      }).add({
        targets: '.ml8 .bang',
        scale: [0, 1],
        rotateZ: [45, 15],
        duration: 1200,
        offset: '-=1000'
      }).add({
        targets: '.ml8',
        opacity: 0,
        duration: 1000,
        easing: "easeOutExpo",
        delay: 1400
      });

    anime({
      targets: '.ml8 .circle-dark-dashed',
      rotateZ: 360,
      duration: 8000,
      easing: "linear",
      loop: true
    });
  }
  animate() {
    for (let index = 0; index < this.strText.length; index++) {
      const strId = "text" + index + "Id";
      const animatedChar = document.getElementById(strId);
      const interval = setInterval(() => {
        if (animatedChar.style.top === "0px") {
          animatedChar.style.top = '-20px';
        } else {
          animatedChar.style.top = 0;
        }
      }, Math.floor(Math.random() * 900) + 500);
      this.textIntervals.push(interval);
    }
  }
  clearAnimate() {
    for (let index = 0; index < this.textIntervals.length; index++) {
      const interval = this.textIntervals[index];
      clearInterval(interval);
    }
  }
  /* Lifecycle Events */
  componentDidMount() {
    this.loadText();
  }
  render() {
    return (
      <div id="animatedTextId" className="animated-text">
        <h1 class="ml8">
          <span class="letters-container">
            <span class="letters letters-left">Hi</span>
            <span class="letters bang">!</span>
          </span>
          <span class="circle circle-white"></span>
          <span class="circle circle-dark"></span>
          <span class="circle circle-container"><span class="circle circle-dark-dashed"></span></span>
        </h1>
      </div>
    );
  }
}
