import React from 'react';
import './TopTitleContainer.scss';

import AnimateTextContainer from '../../components/AnimateTextContainer/AnimateTextContainer';

export default class TopTitleContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dimensions: {}
        };
    }
    /* Lifecycle Evenets */
    componentDidMount() {
        this.setState({
            dimensions: {
                height: window.screen.height
            }
        });
    }
    render() {
        const { dimensions } = this.state;
        return (
            <div className="top-title-container" style={{ minHeight: dimensions.height + 'px' }}>
                <div className="animated-heading centered">
                    <AnimateTextContainer></AnimateTextContainer>
                </div>
                <div className="role-title centered">I am a FULL STACK DEVELOPER</div>
                <div className="description centered">
                    <span className="title">About Me</span>
                    <span>I started my journey in programming after completing BE in Computer Engineering in 2009,
                    since then, I have progressed through time by learning various skills on the way and learning new things had always been challenging but exciting too.
                    Beginning from developing small scale iPhone application, I feel proud to have accomplished success in deploying large scale enterprise web application.
                    Currently working as a Lead Full Stack Developer for Client Baker Hughes.
                </span>
                </div>
            </div>
        );
    }
}
