
export class Project {
    id = null;
    title = "";
    code = "";
    summary = "";
    background = "transparent";
    bullets = [];

    constructor(data) {
        if (data) {
            this.title = data.title;
            this.code = data.code;
            this.summary = data.summary;
            this.bullets = data.bullets;
            this.id = data.id;
        }
    }
}

export function getProjects() {
    let projects = [];
    for (let index = 0; index < 7; index++) {
        const proj = new Project();
        switch (index) {
            case 0:
                proj.id = 0;
                proj.title = "Workbench";
                proj.code = "Workbench";
                proj.background = "transparent";
                proj.summary = "Managing orders, equipments, repair estimates, users through Web/Tablet and Mobile application Baker Hughes";
                proj.bullets.push('Create architecture for Angular 7 application and connecting to backend interfaces.');
                proj.bullets.push("Managing Role Access and integrating auth guards according to the roles.");
                proj.bullets.push("Update SQL queries locally to test role specific scenarios from DB -> JAVA -> Frontend.");
                proj.bullets.push("Write frontend code using Angular 7, Typescript, Ag-Grid components.");
                proj.bullets.push("Integrating media queries for managing responsiveness for various devices Mobile -> Large Desktops.");
                proj.bullets.push("Updating JAVA backend code as per recommended API inputs in Frontend.");
                proj.bullets.push("Assist backend team for API data structuring and its benefits.");
                break;
            case 1:
                proj.id = 1;
                proj.title = "LiftView";
                proj.code = "LiftView";
                proj.background = "#d36b00";
                proj.summary = "Supply Chain Management, Machine Assembly BHGE";
                proj.bullets.push('Creating new modules for already available web application using Angular 7 and update existing components.');
                proj.bullets.push("Gather all the new module related requirements for the project.");
                proj.bullets.push("Designing reusable drag drop containers for user to easily drag machine parts to assembly area.");
                proj.bullets.push("Using Latest Angular 7 components for application development.");
                proj.bullets.push("Integrate Java REST APIs.");
                break;
            case 2:
                proj.id = 2;
                proj.title = "Vtrack";
                proj.code = "Vtrack";
                proj.background = "#5b00d3";
                proj.summary = "Reporting Tool for GE Oil and Gas BHGE";
                proj.bullets.push('Creating a web application using Angular 4 from scratch.');
                proj.bullets.push("Gather all the mock related requirements for the project.");
                proj.bullets.push('Designing reusable multi select component for filtering data set.');
                proj.bullets.push("Using Angular 4 components for application development.");
                break;
            case 3:
                proj.id = 3;
                proj.title = "Appmobi iOS framework";
                proj.code = "Appmobi iOS";
                proj.background = "#d3004e";
                proj.summary = "Appmobi Secure Mobile Platform Native framework for iOS";
                proj.bullets.push('Lead role in designing the architecture to development.');
                proj.bullets.push("Integrating Secure Push Messaging, Secure Analytics and Secure Data Store.");
                proj.bullets.push("Using Symmetric/Asymmetric Cryptography (AES/RSA​) for encryption/decryption.");
                break;
            case 4:
                proj.id = 4;
                proj.title = "Appmobi Cordova plugin";
                proj.code = "Appmobi Cordova";
                proj.background = "#d39e00";
                proj.summary = "Appmobi Secure Mobile Platform Cordova";
                proj.bullets.push('Lead role in designing the architecture to development.');
                proj.bullets.push("Coordinating with Apache Cordova team for resolving Cordova-iOS v4.0.0 Beta related issues.");
                proj.bullets.push("Integrating Secure Push Messaging, Secure Analytics, Secure Live Update and Secure Data Store.");
                break;
            case 5:
                proj.id = 5;
                proj.title = "My Portfolio";
                proj.code = "My Portfolio";
                proj.background = "#007bff80";
                proj.summary = "My Portfolio Creating my portfolio using React.js";
                proj.bullets.push('Design React components to integrate in project.');
                proj.bullets.push("Integrating SCSS style sheets for effective style and appearance.");
                proj.bullets.push("Integrate various JS library with the project for various animate effects.");
                proj.bullets.push("Creating production build and pushing to Github pages for deployment.");
                break;
            case 6:
                proj.id = 6;
                proj.title = "TicTacToe";
                proj.code = "TicTacToe";
                proj.background = "#117a8b";
                proj.summary = "TicTacToe : Creating using SwiftUI";
                proj.bullets.push('Developing a game where 2 players try to make consequent 3 crossed(X) or circles(O) to win the game.');
                proj.bullets.push("Created using MVVM design pattern and chose the Swift programming language for integrating views as a function of state model approach.");
                break;

            default:
                break;
        }
        projects.push(proj);
    }
    return projects;
}