import React, { useState } from 'react';
import Carousel from 'react-bootstrap/Carousel'

import './ProjectsContainer.scss';
import * as ProjectModel from '../../models/project';

export default class ProjectsContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dimensions: {},
      projects: ProjectModel.getProjects()
    };
    this.wrapper = React.createRef();
  }
  animateNow() {

  }
  /* Lifecycle Evenets */
  componentDidMount() {
    this.setState({
      dimensions: {
        height: window.screen.height
      }
    });
  }
  render() {
    const { dimensions, projects } = this.state;
    return (
      <div ref={this.wrapper} className="projects-container" style={{ minHeight: dimensions.height + 'px' }}>
        <div className="common-title-container" style={{ height: 65 + 'px' }}>
          <span>Projects</span>
        </div>
        <div className="projects-content" style={{ minHeight: (dimensions.height - 65) + 'px' }}>
          <Carousel>
            {
              projects.map((project, pIndex) => (
                <Carousel.Item key={pIndex} style={{ minHeight: (dimensions.height - 65) + 'px' }}>
                  <div class="bg" style={{background: project.background}}></div>
                  <Carousel.Caption>
                    <br />
                    <br />
                    <h3>{project.title}</h3>
                    <br />
                    <h5>{project.summary}</h5>
                    <br />
                    {
                      project.bullets.map((bullet, bIndex) => (
                        <div>
                          <li key={bIndex}>{bullet}</li>
                          <div class="spacer"></div>
                        </div>
                      ))
                    }
                  </Carousel.Caption>
                </Carousel.Item>
              ))
            }
          </Carousel>
        </div>
      </div>
    );
  }
}

